.product-sale-hot {
	margin-top: 45px;

	@media(max-width: 1024px) {
		margin-top: 10px;

		.container {
			padding: 0;
		}

		.tab-container {
			.tab-content {
				.content {
					display: block;
					opacity: 1;

					&[tab-id="sale"] {
						margin-bottom: 15px;
					}
				}

				.product-slider {
					padding-bottom: 0;
				}
			}

			.block-title {
				display: none;
			}
		}
	}
}

.home-product {
	margin-top: 55px;

	@media(max-width:1024px) {
		margin-top: 15px;

		.container {
			padding: 0;
		}
	}
}

.product-slider {
	position: relative;
	background: #ffffff;
	border-radius: 0 0 5px 5px;
	padding-bottom: 25px;

	.swiper-wrapper {
		padding: 20px 0 55px;

		.swiper-slide {
			user-select: none;
			padding: 0 30px;

			+.swiper-slide {
				border-left: 1px solid darken(white, 12%);
			}

			figure {
				position: relative;

				.box-img {
					height: 100%;
					width: 100%;
					display: flex;
					align-items: center;

					img {
						object-fit: cover;
						display: block;
						margin: 0 auto;
					}
				}

				figcaption {
					color: #1C1C1C;

					h5 {
						text-align: center;
						font-size: 15px;
					}

					.price {
						display: flex;
						justify-content: center;
						font-size: 15px;
						font-weight: 700;
						margin-bottom: 15px;
						align-items: center;

						del {
							margin-left: 10px;
							font-size: 13px;
							color: #888888;
							font-weight: 400;
						}
					}

					.list-button {
						display: flex;
						justify-content: space-between;

						.add-cart {
							display: flex;
							background: transparent;
							border: none;

							span {
								color: #000000;
								font-size: 10px;
								align-self: flex-end;
								margin-left: 5px;
							}
						}

						.so-sanh {
							display: flex;
							align-items: flex-end;
							text-decoration: underline;
							font-size: 14px;
							font-weight: 700;
							color: #1C1C1C;
						}
					}
				}
			}
		}
	}

	.swiper-button-next {
		background-image: url(../img/icons/next-sale-hot.svg) !important;
		background-size: cover;
		width: 62px;
		height: 62px;
		right: -35px;
	}

	.swiper-button-prev {
		background-image: url(../img/icons/prev-sale-hot.svg) !important;
		background-size: cover;
		width: 62px;
		height: 62px;
		left: -35px;
	}

	.see-more {
		font-size: 16px;
		cursor: pointer;
		width: 140px;
		height: 33px;
		margin: 0 auto;
	}

	@media (max-width: 1024px) {
		.swiper-wrapper {
			padding: 20px 0;

			.swiper-slide {
				padding: 0 50px;
			}
		}
	}

	@media(max-width: 768px) {
		.swiper-wrapper {

			.swiper-slide {
				padding: 0 10px;

				figure {
					.box-img {
						height: unset;
						width: unset;
					}

					figcaption {

						h5 {
							font-size: 14px;
							margin-bottom: 12px;
							font-weight: 400;
							color: #000000;
						}

						.price {
							font-size: 16px;
							display: block;
							margin-bottom: 5px;
							color: #D82F25;
							text-align: center;

							span {
								display: block;
							}

							del {
								display: none;
							}
						}

						.list-button {
							display: none;
						}

						.button-add-cart-mb {
							display: flex;
							margin: 0 auto;
						}
					}
				}
			}
		}

		.swiper-button-next,
		.swiper-button-prev {
			display: none;
		}
	}
}

