.brand-slider {
	background: #ffffff;
	padding: 20px 100px;
	position: relative;
	border-bottom: 1px solid #DFDFDF;

	.swiper-slide {
		.box-img {
			max-width: 130px;
			height: 60px;

			img {
				width: 100%;
				object-fit: cover;
			}
		}
	}

	.swiper-button-next {
		background-image: url(../img/icons/next-brand.svg);
		background-size: 8px;
		background-repeat: no-repeat;
		height: 30px;
		width: 30px;
		transform: translateY(-50%);
		margin-top: unset;
	}

	.swiper-button-prev {
		background-image: url(../img/icons/prev-brand.svg);
		background-size: 8px;
		background-repeat: no-repeat;
		height: 30px;
		width: 30px;
		transform: translateY(-50%);
		margin-top: unset;
	}

	@media(max-width: 1024px) {
		display: none;
	}
}